*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: $fz-default;
  line-height: 20px;
  font-family: $ff-lexend;
  color: $color-default-white;


  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  //      
  text-rendering: optimizeLegibility;
  //       
  // https://transfonter.org/ -   https://prnt.sc/12rnt6g   
}

body {
  width: 100%;
  height: 100%;

  background-color: $color-blackcurrant;
  background-image: url('../img/home-page-bg-1920.png');
  background-repeat: no-repeat;
  background-size: cover;

  @include vp-1023 {
    background-image: url('../img/bg-mobile.png');
  }
}

a {
  color: $color-default-white;
  text-decoration: none;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus,
    &:focus-visible,
    &:focus-within {
      color: $color-cornflower-blue;
      text-decoration: underline;
    }
  }
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

// chrome autofill background removal
//         \ -
//          
// rgba  ,    hex  
//      box-shadow    -
//     `background-color 10000000s ease-out`
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-default-white;

  -webkit-text-fill-color: $color-default-black;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

h1,
h2,
p {
  margin: 0;
}
